import {CHANGE} from "../constant"


const initState = [
  { name: "", check: false},
  { name: "", check: false },
  { name: "", check: false },
  { name: "", check: false },
  { name: "", check: false },
  { name: "", check: false },
  { name: "", check: false },
  { name: "", check: false },
  { name: "", check: false },
  { name: "", check: false },
  { name: "", check: false },
  { name: "", check: false },
  { name: "", check: false },
];

function sidebarReducer(preState = initState, action) {
  const { type, data } = action;
  let copyArray = [...preState];
  let newState;
  switch (type) {
    case CHANGE:
      newState = copyArray.map((dist) => {
        if (dist.name === data.name) {
          return ({name:data.name,check:data.check});
        } else {
          return dist;
        }
      });
      return newState;
    default:
      return preState;
  }
}

export default sidebarReducer;
