import React, { useState } from "react";
import {
  Box,
  FormGroup,
  FormControlLabel,
  TextField,
  Autocomplete,
  Checkbox,
} from "@mui/material";
import { connect } from "react-redux";
import { checkBoxChange } from "../../redux/actions/sideBar";

function SideBar(props) {
  //dispatch function
  const { checkChange,District } = props;

  //
  const taipei = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];

  //
  const [city, setCity] = useState([
    { label: "" },
    { label: "()" },
    { label: "()" },
    { label: "()" },
  ]);

  //
  const [district, setDistrict] = useState([]);

  //
  

  // 
  function handleCheckBox(event) {
    const currentCheck = event.target.checked;
    const currentLabelText = event.nativeEvent.path[2].outerText;
    const data = { name: currentLabelText, check: currentCheck };
    checkChange(data);
  }

  // 
  function handleInputChange(e) {
    switch (e.target.textContent) {
      case "":
        setDistrict(taipei);
        break;
      default:
        setDistrict([]);
    }
  }



  return (
    <Box sx={{ gridArea: "sidebar", marginTop: "5vmin" }}>
      <Autocomplete
        disablePortal
        id="combo-box"
        options={city}
        sx={{ width: "100%" }}
        blurOnSelect="mouse"
        onInputChange={handleInputChange}
        renderInput={(params) => (
          <TextField {...params} color="searchColor" label="" />
        )}
      />
      <FormGroup
        sx={{
          flexDirection: "row",
          width: "100%",
        }}
      >
        {district.map((district, index) => {
          return (
            <FormControlLabel
              label={district}
              onChange={handleCheckBox}
              sx={{ flexGrow: 1, marginTop: "2vmin" }}
              key={index}
              control={
                <Checkbox
                  // defaultChecked
                  sx={{
                    color: "#e1dad0",
                    "&.Mui-checked": {
                      color: "#755433",
                    },
                  }}
                  inputProps={{ "aria-label": "controlled" }}
                />
              }
            />
          );
        })}
      </FormGroup>
    </Box>
  );
}

export default connect((state) => ({ District: state.checkBox }), {
  checkChange: checkBoxChange,
})(SideBar);

